import React, { useEffect, useState } from "react";
import styles from './Register.module.css';
import axios from 'axios';

const Register = () => {

    const [idMessage, setIdMessage] = useState(""); //    
    const [id, setId] = useState(""); //  
    const [idCheckResult, setIdCheckResult] = useState(false); //   

    const [password, setPassword] = useState(""); //  :  
    const [confirmPassword, setConfirmPassword] = useState(""); //  :   
    const [pwMessage, setPwMessage] = useState(""); //   
    const [pwMessageStyle, setPwMessageStyle] = useState(''); //   
    const [pwCheckResult, setPwCheckResult] = useState(false); // 

    const [name, setName] = useState(""); //   
    const [birthDate, setBirthDate] = useState(""); //   

    const [mobileCarrier, setMobileCarrier] = useState("SKT"); //  

    const [tel1, setTel1] = useState("010"); //    
    const [tel2, setTel2] = useState(""); //    
    const [tel3, setTel3] = useState(""); //    
    const [resultMessage, setResultMessage] = useState(""); //   

    // ---------------- ,     ------------------

    //      
    const checkId = async () => {
        //       
        if (!id.trim()) {
            setIdMessage(' .');
            return;
        }
        // dto       
        const sendId = { m_id: id }
        try {
            //  GET      
            const response = await axios.post(
                'http://localhost:8080/underdog/register/id/check',
                sendId,
                { headers: { 'Content-Type': 'application/json; charset=UTF-8' } }
            );

            //     
            console.log(response.data);
            setIdMessage(response.data);  //  'message'   
            if (response.data === "  .") {
                setIdCheckResult(true);
            }
        } catch (error) {
            //     ,    
            setIdMessage('  ');
        };
    }
    // ---------------- ,     ------------------

    // ---------------- ,    --------------------------------------------

    //   
    const handlePasswordChange = (e) => {
        const newPassword = e.target.value;
        setPassword(newPassword);

        //    ( 8 , , ,  )
        const passwordPattern = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,16}$/;
        if (!passwordPattern.test(newPassword)) {
            setPwMessage(' 8~16, , ,   .');
            setPwMessageStyle(styles.errorMessage);
        } else {
            setPwMessage('');
            setPwMessageStyle('');
        }
    };

    //    
    const handleConfirmPasswordChange = (e) => {
        const newConfirmPassword = e.target.value;
        setConfirmPassword(newConfirmPassword);
    };

    //    
    useEffect(() => {
        if (confirmPassword && confirmPassword !== password) {
            setPwMessage('  .');
            setPwMessageStyle(styles.errorMessage);
        } else if (confirmPassword) {
            setPwMessage('');
            setPwMessageStyle('');
            setPwCheckResult(true);
        }
    }, [confirmPassword, password, pwCheckResult]);

    // ---------------- ,    ------------------------------------------------------------

    //    
    const selectCarrier = (e) => {
        setMobileCarrier(e.target.value);
    };

    //    :   
    const handleTel1Change = (e) => setTel1(e.target.value);
    //    :   
    const handleTel2Change = (e) => setTel2(e.target.value);
    //    :   
    const handleTel3Change = (e) => setTel3(e.target.value);

    //  useeffact
    useEffect(() => { },
        //     .
        [id, password, mobileCarrier,
            tel1, tel2, tel3, name, birthDate,
            idMessage, idCheckResult, resultMessage]
    );

    //  
    //    
    const sendRegisterData = async (e) => {
        console.log("")
        e.preventDefault();

        if (idCheckResult) {
            if (pwCheckResult) {

                const regiData = {
                    m_id: id,
                    m_pw: password,
                    e_name: name,           //  
                    e_birth: birthDate,     //  
                    e_carrier: mobileCarrier,
                    e_tel_num: tel1 + tel2 + tel3,
                };

                try {
                    console.log(regiData);
                    const response = await axios.post('http://localhost:8080/underdog/register/set', regiData);
                    console.log(response.data);
                    alert(" .");
                    window.close();
                } catch (error) {
                    alert(" .");
                }
            } else {
                setResultMessage("   .");
            }

        } else if (pwCheckResult) {
            setResultMessage("   .");
        } else {
            setResultMessage("   .    .");
        };

    };

    return (
        <form onSubmit={sendRegisterData}>

            {/* ----------------------- ,     -------------------*/}

            {/*   */}
            <input type="text" id={styles.inputId} placeholder=" " value={id}
                onChange={(e) => setId(e.target.value)} //     
                required />
            {/*     */}
            <button type="button" id={styles.idCheckButton} onClick={checkId}>
                  
            </button>
            {/*       */}
            <div id={idMessage === "  ." ? styles.successMessage : styles.errorMessage}>
                {idMessage}
            </div>

            {/* ------------------------- ,     ------------------*/}

            {/* ------------------------- ,    ------------------*/}


            {/*    */}
            <input
                id={styles.pw}
                placeholder=""
                type="password"
                value={password}
                onChange={handlePasswordChange}
                required
            />

            <br />
            {/*     */}
            <input
                id={styles.confirmPw}
                placeholder=" "
                type="password"
                value={confirmPassword}
                onChange={handleConfirmPasswordChange}
                required
            />
            <br />
            {/*   */}
            <div id="message" className={pwMessageStyle}>
                {pwMessage}
            </div>

            {/* ------------------------- ,    ------------------*/}
            
            {/* ------------------------- ,    ------------------*/}
            
            {/*   */}
            <input
                type="text"
                id={styles.name}
                className={styles.name}
                placeholder=" "
                value={name}
                onChange={(e) => setName(e.target.value)}
                required
            />

            {/*   */}
            <label htmlFor="birthDate">:</label>
            <input
                type="date"
                id={styles.birthDate}
                className={styles.birthDate}
                value={birthDate}
                onChange={(e) => setBirthDate(e.target.value)}
                required
            />

            {/* ------------------------- ,    ------------------*/}

            {/*    */}
            <select id={styles.carrierSelectBox} onChange={selectCarrier} value={mobileCarrier}>
                <option value='SKT'>SKT</option>
                <option value='KT'>KT</option>
                <option value='LGU+'>LGU+</option>
                <option value=''></option>
            </select>

            {/*    */}
            <label htmlFor="tel">:</label>
            <input type="tel" id={styles.tel_1} maxLength="3" value={tel1} onChange={handleTel1Change} required /> -
            <input type="tel" id={styles.tel_2} maxLength="4" value={tel2} onChange={handleTel2Change} required /> -
            <input type="tel" id={styles.tel_3} maxLength="4" value={tel3} onChange={handleTel3Change} required />

            {/*   */}
            <button type="submit" className={styles.button} ></button>
            {/*          */}
            {resultMessage}
        </form>
    );
};

export default Register;
