import React, { useState, useEffect } from 'react';
import axios from 'axios';
import styles from './Mypage.module.css';

const Mypage = () => {
  const [userInfo, setUserInfo] = useState(null); //   
  const [error, setError] = useState(null); //  
  const [editMode, setEditMode] = useState(false); //   
  const [formData, setFormData] = useState({
    m_id: '',
    m_pw: '',
    a_authority: false,
    p_authority: false,
    e_authority: false,
    e_name: '',
    e_birth: '',
    e_carrier: '',
    e_tel_num: '',
    m_key: null,
  });

  const localM_id = localStorage.getItem('m_id'); //    ID
  const sessionM_id = sessionStorage.getItem('m_id'); //    ID

  //   
  useEffect(() => {
    const fetchUserInfo = async () => {
      setError(null); //  
      const m_id = localM_id == null ? sessionM_id : localM_id;

      if (!m_id) {
        setError('  .  .');
        return;
      }

      try {
        const response = await axios.get('http://localhost:8080/underdog/mypage/userinfo', {
          params: { m_id },
        });

        if (response.data.status === 'success') {
          setUserInfo(response.data);

          const member = response.data.memberInfo;
          const employee = response.data.employeeInfo;

          //  
          setFormData({
            m_id: member.m_id,
            m_pw: member.m_pw,
            a_authority: member.a_authority,
            p_authority: member.p_authority,
            e_authority: member.e_authority,
            e_name: employee.e_name,
            e_birth: employee.e_birth,
            e_carrier: employee.e_carrier,
            e_tel_num: employee.e_tel_num,
            m_key: employee.m_key,
          });
        } else {
          setError('    .');
        }
      } catch (err) {
        console.error(err);
        setError('  .');
      }
    };

    fetchUserInfo();
  }, [localM_id, sessionM_id]);

  const logout = () => {
    if (window.opener) {
      window.opener.sessionStorage.clear();
      window.opener.localStorage.clear();
      window.opener.location.reload(true);
    }
    window.close();
  };

  //   
  const handleInputChange = (e) => {
    const { name, value } = e.target;

    //  
    setFormData({ ...formData, [name]: value });
  };

  //  
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('http://localhost:8080/underdog/mypage/updateInfo', formData);

      if (response.data.status === 'success') {
        alert(' .');

        //    
        const updatedEmployeeInfo = response.data.employeeInfo || {
          e_name: formData.e_name,
          e_birth: formData.e_birth,
          e_carrier: formData.e_carrier,
          e_tel_num: formData.e_tel_num,
        };

        setUserInfo((prevState) => ({
          ...prevState,
          employeeInfo: updatedEmployeeInfo,
        }));

        setEditMode(false); //   
      } else {
        alert(' : ' + response.data.message);
      }
    } catch (error) {
      console.error(error);
      alert('   .');
    }
  };

  if (error) return <p className={styles.error}>{error}</p>;

  return (
    <div className={styles.mypageContainer}>
      <h2> </h2>
      {userInfo ? (
        editMode ? (
          <form onSubmit={handleSubmit}>
            <div>
              <label>:</label>
              <input
                type="password"
                name="m_pw"
                value={formData.m_pw}
                onChange={handleInputChange}
                placeholder=""
              />
            </div>
            <div>
              <label>:</label>
              <input
                type="text"
                name="e_name"
                value={formData.e_name}
                onChange={handleInputChange}
                placeholder=""
              />
            </div>
            <div>
              <label>:</label>
              <input
                type="date"
                name="e_birth"
                value={formData.e_birth}
                onChange={handleInputChange}
              />
            </div>
            <div>
              <label>:</label>
              <input
                type="text"
                name="e_tel_num"
                value={formData.e_tel_num}
                onChange={handleInputChange}
                placeholder=""
              />
            </div>
            <button type="submit"></button>
            <button type="button" onClick={() => setEditMode(false)}>
              
            </button>
          </form>
        ) : (
          <div className={styles.infoBox}>
            <p>
              <strong>:</strong> {userInfo.memberInfo.m_id}
            </p>
            <p>
              <strong>:</strong> {userInfo.employeeInfo.e_name}
            </p>
            <p>
              <strong>:</strong> {userInfo.employeeInfo.e_birth || ' '}
            </p>
            <p>
              <strong>:</strong> {userInfo.employeeInfo.e_tel_num}
            </p>
            <button onClick={() => setEditMode(true)}> </button>
            <button id={styles.logoutButton} onClick={logout}>
              
            </button>
          </div>
        )
      ) : (
        <p>  ...</p>
      )}
    </div>
  );
};

export default Mypage;
